/*CSS RESET*/


/*---------------------------------------------------------------------------*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}


/* HTML5 display-role reset for older browsers */

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

body {
    scroll-behavior: smooth;
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

table {
    border-collapse: collapse;
    border-spacing: 0;
}

* {
    box-sizing: border-box;
}

strong {
    font-weight: 600;
}


/*----------------------------------------------------------------------------*/


/*Full width & height*/

.full-height {
    height: 100%;
}

.full-width {
    width: 100%;
}


/*Button*/

button {
    border-radius: 0;
}


/*Icon*/

.icon {
    cursor: pointer;
    display: inline-block;
    /*line-height: 1;*/
    outline: none;
}


/*Letter Spacing*/

.letterspacing-normal {
    letter-spacing: normal !important;
}

.letterspacing-2em {
    letter-spacing: 0.2em;
}

.letterspacing-1em {
    letter-spacing: 0.1em;
}

.letterspacing-15em {
    letter-spacing: 0.15em;
}

.letterspacing-25em {
    letter-spacing: 0.25em;
}


/*Flexbox*/

.d-flex {
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: box;
    display: flex;
}

.vertical-align {
    -webkit-align-items: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

.vertical-align-end {
    -webkit-align-items: flex-end;
    -webkit-box-align: flex-end;
    -ms-flex-align: flex-end;
    align-items: flex-end;
}

.flex-content-end {
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
}

.flex-content-start {
    -webkit-justify-content: flex-start;
    justify-content: flex-start;
}

.flex-content-center {
    -webkit-justify-content: center;
    justify-content: center;
}

.flex-content-spacebetween {
    -webkit-justify-content: space-between;
    justify-content: space-between;
}

.flex-content-spacearound {
    -webkit-justify-content: space-around;
    justify-content: space-around;
}

.flex-nowrap {
    -webkit-flex-wrap: nowrap;
    flex-wrap: nowrap;
}

.flex-wrap {
    -webkit-flex-wrap: wrap;
    flex-wrap: wrap;
}

.fw-medium {
    font-weight: 500;
}

.fw-regular {
    font-weight: 400;
}

.fw-bold {
    font-weight: 700;
}


/*Display*/

.d-block {
    display: block !important;
}

.d-inline {
    display: inline-block !important;
}

.hidden {
    display: none !important;
}


/*Position*/

.pos-absolute {
    position: absolute !important;
}

.pos-relative {
    position: relative !important;
}

.pos-fixed {
    position: fixed !important;
}


/*Overflow*/

.overflow-hidden {
    overflow: hidden;
}


/*Border Styles*/

.border-dotted {
    border-style: dotted;
}

.border-solid {
    border-style: solid;
}

.border-dashed {
    border-style: dashed;
}

.border-hidden {
    border-style: hidden;
}

.border-double {
    border-style: double;
}


/*Cursor*/

.clickable,
.clickable:focus {
    cursor: pointer;
    outline: none;
    -webkit-user-select: none;
    /* Chrome all / Safari all */
    -moz-user-select: none;
    /* Firefox all */
    -ms-user-select: none;
    /* IE 10+ */
    user-select: none;
    /* Likely future */
}

.not-allowed {
    cursor: not-allowed !important;
}

.auto-cursor {
    cursor: auto !important;
}

.move-cursor {
    cursor: move !important;
}


/*Outline*/

.no-outline {
    outline: 0 none !important;
}


