/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}

body {
  scroll-behavior: smooth;
  line-height: 1;
}

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

strong {
  font-weight: 600;
}

/*----------------------------------------------------------------------------*/
/*Full width & height*/
.full-height {
  height: 100%;
}

.full-width {
  width: 100%;
}

/*Button*/
button {
  border-radius: 0;
}

/*Icon*/
