  font-family: "Work Sans", sans-serif;
  font-weight: 300;
}

p, address {
  font-family: "Work Sans", sans-serif;
  font-size: .75rem;
  line-height: 1.25rem;
}

h1 {
  font-size: 60px;
  color: #242121;
  font-weight: 300;
  text-transform: uppercase;
}

h2 {
  font-size: 35px;
  font-weight: 300 !important;
  text-transform: uppercase;
}

h2, h3 {
  position: relative;
  text-transform: uppercase;
}

h3 {
  font-size: 30px;
}

h4 {
  font-size: 18px;
}

.fs_20 {
  font-size: 20px !important;
}

.fs_18 {
  font-size: 18px !important;
}

.fs_16 {
