/*load responsive*/
@media (max-width: 991px) {
  #info {
    padding: 7em 2em 1em 2em !important;
    text-align: center;
  }
}

@media (max-width: 768px) {
  #contact-us h3 {
    margin-bottom: .5em !important;
  }
  #info {
    margin-top: 4em;
  }
  #info .col-md-7 {
    max-width: 100% !important;
  }
  .notfound .notfound-404 {
    height: 200px;
  }
  .notfound .notfound-404 h1 {
    font-size: 200px;
  }
}

@media (max-width: 767.98px) {
  header {
    height: 80px;
  }
  header .brand-logo {
    height: 80px;
    width: 80px;
  }
  #our-mission {
    padding-top: 3rem;
  }
  #about-us {
    padding: 3rem 0;
  }
  #about-us img {
    padding-top: 3rem;
  }
  .btn-1 {
    margin-top: 2rem;
  }
  #how-can-we-help {
    padding-bottom: 3rem;
  }
  #contact-us {
    padding: 3rem 0;
  }
  #contact-form {
    padding-bottom: 3rem;
  }
  #footer-bg {
    padding: 5rem 0;
  }
  #footer-bg .footer-nav {
    display: block !important;
    text-align: center;
  }
  #footer-bg .footer-nav a {
    cursor: pointer;
  }
  #offer .col-lg-4 {
    display: block !important;
    text-align: center;
  }
  #offer .col-lg-4 .fas {
    padding-bottom: .8em;
  }
  #offer,
  #expertise {
    padding: 3rem 0;
  }
  #offer .col-lg-4:not(:last-child) {
    padding-bottom: 3rem;
  }
  #expertise .progress-wrapper {
    padding-bottom: 3rem;
  }
  #magic-section {
    padding-bottom: 3rem;
  }
  #magic-section h3 {
    padding-top: 3rem;
  }
}

@media only screen and (max-width: 480px) {
  .notfound .notfound-404 {
    height: 162px;
  }
  .notfound .notfound-404 h1 {
    font-size: 162px;
    height: 150px;
    line-height: 162px;
  }
  .notfound h2 {
    font-size: 16px;
  }
}

